*{
    padding:0;
    margin:0;
}

.Nav-Parent{
    top:0;
    width:100%;
    background-color: rgba(0, 0, 0, 0.834);
    color:white;
    /* position: sticky; */
}

.Nav-Child{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding:0.5cm 1cm;
    /* border-bottom: thin solid rgb(58, 58, 58); */
}

.Menu-Parent{
    width:fit-content;
}

.Menu-Child{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    align-items:center;
}

.btn2{
    font-size: 14px;
    color:white;
    border:5px solid rgb(134, 53, 9);
    background-color: transparent;
    padding:0.1cm 0.5cm;
    border-radius: 18px;
    
  }
  
  .btn2:hover{
    background-color: rgb(134, 53, 9);
  }

.menu-head{
    font-size: 16px;
    font-weight: bolder;
    font-family: "Source Code Pro", monospace;
    text-decoration: none;
    color:white;
}

.active{
    color:white;
}

.logoImg{
    width:6cm;
    height:auto;
}

.signOut{
    width:0.8cm;
}

#navDiv{
    width:6cm;
    /* height:100vh; */
    right:0;
    top:0;
    bottom:0;
    display: none;
}

.overlay-nav-div {
    position: fixed;
    background-color: transparent;
    z-index: 100;
  }
  
  .overlay-nav-div-section {
    width: 100%;
    height:100vh;
    padding: 0.2cm 0.5cm;
    display: flex;
    flex-direction: column;
    row-gap: 1cm;
    /* align-items: center; */
    border: 1px solid rgb(54, 24, 0);
    background-color: rgb(85, 33, 0);
    /* overflow: auto; */
  }
  
  .edit-head {
    display: flex;
    justify-content: flex-end;
  }

  
.OverlayMenu-Parent{
    width:fit-content;
    color:black;
}

.OverlayMenu-Child{
    display: flex;
    flex-direction: column;
    column-gap: 0.5cm;
    row-gap: 0.5cm;
    align-items:center;
    justify-content: center;
}

.menuImg{
    width:0.8cm;
    display: none;
}

@media only screen and (max-width:780px){
    .menuImg{
        display: flex;
    }
    
    .Menu-Parent{
        display: none;
    }

}