/* SignIn CSS*/

.SignInComp {
  /* background-color: rgb(9, 9, 9); */
  background-image: url("./Images/greenBG.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  background-position: center center;
  /* justify-content: center; */
  align-items: center;
  row-gap: 1cm;
}

.div2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 1cm;
}

.SignInParent {
  width: fit-content;
