  color: rgb(148, 148, 148);
  font-size: 14px;
}

.btnSignIn {
  padding: 0.3cm 0.5cm;
  background-color: rgb(40, 116, 208);
  color: white;
  border-radius: 8px;
  box-shadow: none;
  border: none;
  font-size: 16px;
  margin-top: 0.3cm;
  transition: 0.1s ease-in-out;
  cursor: pointer;
}

.btnSignIn:hover {
  background-color: rgb(0, 94, 208);
  /* transform: scale(1.005); */
  border: none;
}

.btnDisabled{
  pointer-events: none;
  opacity: 0.5;
}

.labelText {
  font-size: 12px;
  /* color: #bf4342; */
  text-align: left;

  color: rgb(129, 129, 129);
}

