  color: rgb(140, 140, 255);
  transition: 0.3s ease;

  &:hover {
    text-decoration: underline;
  }
}

.midText {
  font-size: 16px;
  color: rgb(187, 187, 255);
  text-decoration: none;
  cursor: pointer;

  transition: 0.2s ease;
  padding: 0.1cm 0.4cm 0.2cm 0.4cm;
